export default ".rich-text-modal{max-height:100%;position:fixed;top:50%;left:50%;transform:translate(-50%, -50%);width:200px;padding:10px;margin:0;background:#fafafa;border:1px solid #ccc;box-shadow:2px 2px 10px rgba(0,0,0,.2);cursor:default;z-index:999999999999999;border-radius:.5em;font-family:-apple-system,system-ui,BlinkMacSystemFont,\"Segoe UI\",\"Open Sans\",\"Helvetica Neue\",Helvetica,Arial,sans-serif;font-size:16px;min-width:500px;display:flex;flex-direction:column;align-items:stretch}.rich-text-modal .textarea{resize:none;width:100%;margin-bottom:10px;padding:8px;border-radius:4px;border:1px solid #ccc;background:#fff;color:#000;height:300px}.rich-text-modal .textarea-single-line{resize:none;width:100%;margin-bottom:10px;padding:8px;border-radius:4px;border:1px solid #ccc;background:#fff;color:#000}.rich-text-modal .modal-buttons{display:flex;justify-content:flex-end}.rich-text-modal .modal-buttons button{padding:5px 10px;border:none;border-radius:4px;cursor:pointer;background-color:#0078d7;color:#fff;margin-left:10px}.rich-text-modal .modal-buttons button:hover{background-color:#005fa3}.dark-theme .rich-text-modal{background:#212121;border-color:#212121}.dark-theme .rich-text-modal .textarea,.dark-theme .rich-text-modal .textarea-single-line{background:#303030;color:#fff;border-color:#303030}";