export default ".chat-log{background:#fff;overflow-y:auto}.chat-log img{max-width:100%;height:auto}.chat-log .chat-message{display:flex;flex-direction:column;white-space:pre-wrap;word-wrap:break-word;overflow-wrap:break-word;margin:10px 0 10px 0;padding:0;width:auto}.chat-log .chat-message .sender{color:#333;display:flex;font-weight:bold}.chat-log .chat-message .sender .delete-message,.chat-log .chat-message .sender .spinner{color:#551a8b}.chat-log .chat-message .sender svg{display:block;height:22px;width:auto;margin-right:5px;cursor:pointer}.chat-log .chat-message .sender .spinner{color:#0078d7;animation:spin .8s linear infinite}@keyframes spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.chat-log a{all:unset;color:#06c;text-decoration:none;cursor:pointer}.chat-log a:hover{text-decoration:underline}.chat-log a:visited{color:#551a8b}.chat-log a:active{color:#c00}.dark-theme .chat-log{background:#303030;color:#fff}.dark-theme .chat-log .chat-message .sender{color:#fff}.dark-theme .chat-log .chat-message .sender svg{color:#9730f5}";