export default ".chat-input-container{display:flex;margin:10px 0 0 0}.chat-input-container input[type=text]{flex-grow:1;padding:5px;border:1px solid #ccc;border-radius:4px 0 0 4px;background:#fff;color:#000}.chat-input-container button{padding:5px 10px;border:none;border-radius:4px;cursor:pointer;background-color:#0078d7;color:#fff;border-radius:0 4px 4px 0}.chat-input-container button:hover{background-color:#005fa3}.dark-theme .chat-input-container input{background:#303030;border-color:#303030;color:#fff}";